import {
	Box,
	Paper,
	Stack,
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TablePagination,
	TableRow,
	Typography,
} from '@mui/material'
import CreateMailAddressButton from 'components/Button/CreateMailAddress'
import DeleteButton from 'components/Button/Delete'
import EditButton from 'components/Button/Edit'
import RemoveButton from 'components/Button/Remove'
import EditInput from 'components/EditInput'
import { PERMISSION } from 'interfaces'
import { useEffect, useState } from 'react'
import { Link, useLocation, useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getMailings } from 'redux/slices/Mailing'

export default function Detail() {
	const { id } = useParams<{ id: string }>()
	const location = useLocation()
	const nav = useNavigate()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const status = useAppSelector((s) => s.Mailing.status)
	const list = useAppSelector((s) => s.Mailing.list)
	const mailing = list.find((f) => f.id === parseInt(`${id}`))
	const [page, setPage] = useState(0)
	const [rowsPerPage, setRowsPerPage] = useState(10)
	const dispatch = useAppDispatch()
	useEffect(() => {
		if (status === 'init') dispatch(getMailings())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	useEffect(() => {
		if (status === 'fulfilled' && !mailing) {
			nav('/mailing')
		}
	}, [mailing, nav, status])

	if (!mailing || !tokenInfo) return null
	return (
		<Stack gap={1} my={2}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					color="primary.dark"
					variant="h5"
					sx={{
						fontWeight: 600,
					}}
				>
					メールグループ詳細
				</Typography>
				<Link to={`/mailing/${id}/delete`} state={{ location }}>
					<DeleteButton text="削除" sx={{ width: 150, py: '2px', fontSize: 14 }} />
				</Link>
			</Stack>
			<Stack>
				<EditInput
					label="メールグループ名"
					value={mailing.name}
					apiEndpoint={'/api/mailing/' + id}
					fieldName="name"
					readOnly={tokenInfo.permissions[PERMISSION.MAIL_ADDRESS] < 2}
				/>
				<Box>
					<Link to={`/mailing/${id}/assign`} state={{ location }}>
						<EditButton text="送信可能組織設定" sx={{ width: 150, py: '2px', fontSize: 14 }} />
					</Link>
				</Box>
			</Stack>
			<Paper sx={{ p: 1 }}>
				<Stack gap={2} maxWidth={700}>
					<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
						<Typography>メーリングリストメンバー一覧</Typography>
						<Link to={`/mailing/${id}/member`} state={{ location }}>
							<CreateMailAddressButton text="メールアドレス登録" sx={{ width: 200, py: '2px', fontSize: 14 }} />
						</Link>
					</Stack>
					<TableContainer component={Paper} elevation={3}>
						<Table sx={{ minWidth: 300 }}>
							<TableBody>
								{mailing.mailMembers.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((member) => {
									return (
										<TableRow
											key={member.id}
											sx={{
												'&:last-child td, &:last-child th': { border: 0 },
												'&:hover': {
													bgcolor: '#eee',
												},
											}}
										>
											<TableCell
												sx={{
													p: 1,
												}}
											>
												{member.email}
											</TableCell>
											<TableCell
												sx={{
													p: 1,
												}}
												align="right"
											>
												<Link to={`/mailing/${id}/member/remove/${member.id}`} state={{ location }}>
													<RemoveButton variant="text" color="warning" sx={{ width: 30, py: '2px', fontSize: 14 }} />
												</Link>
											</TableCell>
										</TableRow>
									)
								})}
							</TableBody>
						</Table>
					</TableContainer>
					<TablePagination
						rowsPerPageOptions={[5, 10, 25, 50, 100]}
						component="div"
						count={mailing.mailMembers.length}
						rowsPerPage={rowsPerPage}
						page={page}
						onPageChange={(event: unknown, newPage: number) => {
							setPage(newPage)
						}}
						onRowsPerPageChange={(event: React.ChangeEvent<HTMLInputElement>) => {
							setRowsPerPage(parseInt(event.target.value, 10))
							setPage(0)
						}}
					/>
				</Stack>
			</Paper>
		</Stack>
	)
}
